<template>
  <div class="rehabilitationlist">
    <RehabilitationPrintOption ref="enterEdit"></RehabilitationPrintOption>
    <v-card class="mt-2">
      <v-card-title>
        <v-row>
          <v-col cols="12" md="8">
            <h2 class="subheading grey--text">
              <v-icon class="mx-1">fas fa-file-alt</v-icon>
              <span>
                {{this.$store.state.uData.p_code}} /
                {{this.$store.state.uData.name}}
              </span>
              <span>復健評估表</span>
            </h2>
          </v-col>
          <v-col cols="12" md="4">
            <!-- 功能表 -->
            <v-toolbar dense>
              <v-text-field
                hide-details
                prepend-icon="mdi-magnify"
                single-line
                label="搜尋"
                v-model="serachKey"
                @keyup.stop="search()"
              ></v-text-field>

              <popupadd :viewData="viewData" @getAllData="getAllData" ref="editProcess"></popupadd>

              <v-btn class="primary mx-1" @click="print">
                <v-icon class="mx-1">mdi-printer</v-icon>列印
              </v-btn>
            </v-toolbar>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-text>
        <v-card class="mt-2">
          <v-card-text>
            <!-- 表單內容 -->
            <v-data-iterator
              :items="viewData"
              :items-per-page.sync="itemsPerPage"
              :footer-props="{ itemsPerPageOptions }"
            >
              <template v-slot:default="props">
                <v-simple-table fixed-header class="mt-2 text-no-wrap">
                  <template v-slot:default>
                    <thead style="background-color: #e3f2fd;">
                      <tr>
                        <th rowspan="2"></th>
                        <th rowspan="2">評估日期</th>
                        <th colspan="4">關節活動度（ROM）</th>
                        <th colspan="4">肌肉力量（MMT）</th>
                        <th rowspan="2">動作、姿態與步態</th>
                        <th colspan="5">肌肉張力(Muscle tone)</th>
                        <th colspan="5">平衡（Balance）</th>
                        <th colspan="6">布郎斯壯分期（Brunnstrom’s stage ）(針對中風個案)</th>
                        <th rowspan="2">短期目標（Short term goals）</th>
                        <th rowspan="2">長期目標（Long term goals）</th>
                        <th rowspan="2">治療項目（Treatment programs）</th>
                        <th rowspan="2">紀錄人</th>
                        <th rowspan="2">修改紀錄</th>
                      </tr>
                      <tr>
                        <th>右上肢</th>
                        <th>右下肢</th>
                        <th>左上肢</th>
                        <th>左下肢</th>

                        <th>右上肢</th>
                        <th>右下肢</th>
                        <th>左上肢</th>
                        <th>左下肢</th>

                        <th>軀幹</th>
                        <th>右上肢</th>
                        <th>右下肢</th>
                        <th>左上肢</th>
                        <th>左下肢</th>

                        <th>坐姿靜態</th>
                        <th>坐姿動態</th>
                        <th>站姿靜態</th>
                        <th>站姿動態</th>

                        <th>右上肢-近</th>
                        <th>右上肢-遠</th>
                        <th>左上肢-近</th>
                        <th>左上肢-遠</th>
                        <th>右下肢</th>
                        <th>左下肢</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item,index) in props.items" :key="index">
                        <td>
                          <v-menu transition="scale-transition" offset-y>
                            <template v-slot:activator="{ on }">
                              <v-btn color="primary" dark v-on="on">功能</v-btn>
                            </template>
                            <v-list>
                              <v-list-item @click="edit(item)">
                                <v-list-item-title>修改</v-list-item-title>
                              </v-list-item>
                              <v-list-item @click="del(item)">
                                <v-list-item-title>刪除</v-list-item-title>
                              </v-list-item>
                              <!-- <v-list-item @click.stop="print(item)">
                                <v-list-item-title>列印</v-list-item-title>
                              </v-list-item> -->
                            </v-list>
                          </v-menu>
                        </td>
                        <td>{{item.date}}</td>
                        <td>{{item.rom1}}</td>
                        <td>{{item.rom2}}</td>
                        <td>{{item.rom3}}</td>
                        <td>{{item.rom4}}</td>

                        <td>{{item.mmt1}}</td>
                        <td>{{item.mmt2}}</td>
                        <td>{{item.mmt3}}</td>
                        <td>{{item.mmt4}}</td>

                        <td>{{item.action}}</td>

                        <td>{{item.muscletone1}}</td>
                        <td>{{item.muscletone2}}</td>
                        <td>{{item.muscletone3}}</td>
                        <td>{{item.muscletone4}}</td>
                        <td>{{item.muscletone5}}</td>

                        <td>{{item.balance1}}</td>
                        <td>{{item.balance2}}</td>
                        <td>{{item.balance3}}</td>
                        <td>{{item.balance4}}</td>
                        <td>{{item.balance5}}</td>

                        <td>{{item.brunnstrom1}}</td>
                        <td>{{item.brunnstrom2}}</td>
                        <td>{{item.brunnstrom3}}</td>
                        <td>{{item.brunnstrom4}}</td>
                        <td>{{item.brunnstrom5}}</td>
                        <td>{{item.brunnstrom6}}</td>

                        <td>{{item.shorttermgoals}}</td>
                        <td>{{item.longtermgoals}}</td>
                        <td>{{item.treatmentprograms}}</td>

                        <td>{{item.create_man}}</td>
                        <td>{{item.edit_man}}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </template>
            </v-data-iterator>
          </v-card-text>
        </v-card>
      </v-card-text>
    </v-card>

    <!-- <pre>{{viewData}}</pre> -->
  </div>
</template>

<script>
import qs from "qs";
import popupadd from "@/views/document/RehabilitationAdd";
import RehabilitationPrintOption from "@/components/RehabilitationPrintOption"; //列印前的內容設定
import dayjs from "dayjs";

export default {
  components: { popupadd , RehabilitationPrintOption },
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      viewData: [], //顯示的資料
      serachKey: "",
      edititem: {}, //修改中資料
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,
    };
  },
  mounted() {
    this.getAllData();
  },
  methods: {
    getAllData() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey,
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" +
            this.$store.state.databaseName +
            "/rehabilitation",
          qs.stringify(postData)
        )
        .then((rs) => {
          // console.log(rs.data)
          this.items = this.viewData = rs.data;
          // this.$store.commit("setLoadingOff"); //讀取畫面off
          this.sortBy();
        });
    },
    sortBy() {
      //依name排序
      this.viewData = this.viewData.sort(function (a, b) {
        return parseInt(a.date) < parseInt(b.date) ? 1 : -1;
      });
    },
    edit(item) {
      //修改功能
      this.$refs.editProcess.$emit("fromParent", item);
    },
    del(delData) {
      //刪除功能
      this.$confirm("確認刪除?").then((res) => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey: delData.snkey,
          tablename: "rehabilitation",
          info: JSON.stringify(delData),
        };

        if (res) {
          this.axios
            .post(
              "general/delv3/" +
                this.$store.state.databaseName +
                "/rehabilitation",
              qs.stringify(postData)
            )
            .then((rs) => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
              }
            });
        }
      });
    },
    search() {
      var searchArr = this.serachKey.split(" ");
      var outputitems = [...this.items];
      var str = "";
      searchArr.forEach((key) => {
        if (key != "") {
          outputitems = outputitems.filter((item) => {
            str = JSON.stringify(item).toUpperCase();
            if (str.includes(key.toUpperCase())) {
              return item;
            }
          });
        }
      });
      this.viewData = outputitems;
    },
    print() {
      this.$refs.enterEdit.$emit("childFn", this.items);
    },
    // print(item) {
    //   // var printData = {
    //   //   medicalData: item,
    //   //   medicineData: medicineData
    //   // };

    //   //送出資料到列印頁面
    //   sessionStorage.setItem("printData", JSON.stringify(item));
    //   let routeData = this.$router.resolve({
    //     path: "/print/rehabilitationprint",
    //   });
    //   window.open(routeData.href, "_blank");
    // },
  },
};
</script>


<style lang="scss">
.rehabilitationlist {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1.2rem;
      font-family: "微軟正黑體";
      color: red;
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>