<template>
  <div class="rehabilitationadd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess">mdi-plus-circle</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-form ref="form" class="mt-3">
            <v-row>
              <v-col cols="6">
                <v-text-field label="評估日期" v-model="list.date" type="date" :rules="emptyRules"></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-alert color="primary" border="left" outlined>
                  <v-sheet color="primary dark-1" class="pa-2 white--text" elevation="4">
                    <h2>1.關節活動度（ROM）</h2>
                  </v-sheet>
                  <v-row>
                    <v-col cols="12" sm="3">
                      <v-select :items="romItems" label="右上肢" v-model="list.rom1"></v-select>
                    </v-col>
                    <v-col cols="12" sm="3">
                      <v-select :items="romItems" label="右下肢" v-model="list.rom2"></v-select>
                    </v-col>
                    <v-col cols="12" sm="3">
                      <v-select :items="romItems" label="左上肢" v-model="list.rom3"></v-select>
                    </v-col>
                    <v-col cols="12" sm="3">
                      <v-select :items="romItems" label="左下肢" v-model="list.rom4"></v-select>
                    </v-col>
                  </v-row>
                </v-alert>
              </v-col>
              <v-col cols="12">
                <v-alert color="primary" border="left" outlined>
                  <v-sheet color="primary dark-1" class="pa-2 white--text" elevation="4">
                    <h2>2.肌肉力量（MMT）</h2>
                  </v-sheet>
                  <v-row>
                    <v-col cols="12" sm="3">
                      <v-select :items="mmtItems" label="右上肢" v-model="list.mmt1"></v-select>
                    </v-col>
                    <v-col cols="12" sm="3">
                      <v-select :items="mmtItems" label="右下肢" v-model="list.mmt2"></v-select>
                    </v-col>
                    <v-col cols="12" sm="3">
                      <v-select :items="mmtItems" label="左上肢" v-model="list.mmt3"></v-select>
                    </v-col>
                    <v-col cols="12" sm="3">
                      <v-select :items="mmtItems" label="左下肢" v-model="list.mmt4"></v-select>
                    </v-col>
                  </v-row>
                </v-alert>
              </v-col>
              <v-col cols="12">
                <v-alert color="primary" border="left" outlined>
                  <v-sheet color="primary dark-1" class="pa-2 white--text" elevation="4">
                    <h2>3.動作、姿態與步態</h2>
                  </v-sheet>
                  <v-row>
                    <v-col cols="12">
                      <v-select :items="actionItems" label="動作、姿態與步態" v-model="list.action"></v-select>
                    </v-col>
                  </v-row>
                </v-alert>
              </v-col>
              <v-col cols="12">
                <v-alert color="primary" border="left" outlined>
                  <v-sheet color="primary dark-1" class="pa-2 white--text" elevation="4">
                    <h2>4.肌肉張力(Muscle tone)</h2>
                  </v-sheet>
                  <v-row>
                    <v-col cols="12" sm="2">
                      <v-select :items="muscletoneItems" label="軀幹" v-model="list.muscletone1"></v-select>
                    </v-col>
                    <v-col cols="12" sm="2">
                      <v-select :items="muscletoneItems" label="右上肢" v-model="list.muscletone2"></v-select>
                    </v-col>
                    <v-col cols="12" sm="2">
                      <v-select :items="muscletoneItems" label="右下肢" v-model="list.muscletone3"></v-select>
                    </v-col>
                    <v-col cols="12" sm="2">
                      <v-select :items="muscletoneItems" label="左上肢" v-model="list.muscletone4"></v-select>
                    </v-col>
                    <v-col cols="12" sm="2">
                      <v-select :items="muscletoneItems" label="左下肢" v-model="list.muscletone5"></v-select>
                    </v-col>
                  </v-row>
                </v-alert>
              </v-col>
              <v-col cols="12">
                <v-alert color="primary" border="left" outlined>
                  <v-sheet color="primary dark-1" class="pa-2 white--text" elevation="4">
                    <h2>5.平衡（Balance）</h2>
                  </v-sheet>
                  <v-row>
                    <v-col cols="12" sm="2">
                      <v-select :items="balanceItems" label="坐姿靜態" v-model="list.balance1"></v-select>
                    </v-col>
                    <v-col cols="12" sm="2">
                      <v-select :items="balanceItems" label="坐姿動態" v-model="list.balance2"></v-select>
                    </v-col>
                    <v-col cols="12" sm="2">
                      <v-select :items="balanceItems" label="站姿靜態" v-model="list.balance3"></v-select>
                    </v-col>
                    <v-col cols="12" sm="2">
                      <v-select :items="balanceItems" label="站姿動態" v-model="list.balance4"></v-select>
                    </v-col>
                    <v-col cols="12" sm="4">
                      <v-select :items="balanceItems" label="坐姿到站姿/站姿到坐姿平衡" v-model="list.balance5"></v-select>
                    </v-col>
                  </v-row>
                </v-alert>
              </v-col>
              <v-col cols="12">
                <v-alert color="primary" border="left" outlined>
                  <v-sheet color="primary dark-1" class="pa-2 white--text" elevation="4">
                    <h2>6.布郎斯壯分期（Brunnstrom’s stage ）(針對中風個案)</h2>
                  </v-sheet>
                  <v-row>
                    <v-col cols="12" sm="2">
                      <v-select :items="brunnstromItems" label="右上肢-近" v-model="list.brunnstrom1"></v-select>
                    </v-col>
                    <v-col cols="12" sm="2">
                      <v-select :items="brunnstromItems" label="右上肢-遠" v-model="list.brunnstrom2"></v-select>
                    </v-col>
                    <v-col cols="12" sm="2">
                      <v-select :items="brunnstromItems" label="左上肢-近" v-model="list.brunnstrom3"></v-select>
                    </v-col>
                    <v-col cols="12" sm="2">
                      <v-select :items="brunnstromItems" label="左上肢-遠" v-model="list.brunnstrom4"></v-select>
                    </v-col>
                    <v-col cols="12" sm="2">
                      <v-select :items="brunnstromItems" label="右下肢" v-model="list.brunnstrom5"></v-select>
                    </v-col>
                    <v-col cols="12" sm="2">
                      <v-select :items="brunnstromItems" label="左下肢" v-model="list.brunnstrom6"></v-select>
                    </v-col>
                  </v-row>
                </v-alert>
              </v-col>
              <v-col cols="12">
                <v-alert color="primary" border="left" outlined>
                  <v-sheet color="primary dark-1" class="pa-2 white--text" elevation="4">
                    <h2>7.短期目標（Short term goals）</h2>
                  </v-sheet>
                  <v-row>
                    <v-col cols="12">
                      <v-textarea label="短期目標（Short term goals）" v-model="list.shorttermgoals"></v-textarea>
                    </v-col>
                  </v-row>
                </v-alert>
              </v-col>
              <v-col cols="12">
                <v-alert color="primary" border="left" outlined>
                  <v-sheet color="primary dark-1" class="pa-2 white--text" elevation="4">
                    <h2>8.長期目標（Long term goals）</h2>
                  </v-sheet>
                  <v-row>
                    <v-col cols="12">
                      <v-textarea label="長期目標（Long term goals）" v-model="list.longtermgoals"></v-textarea>
                    </v-col>
                  </v-row>
                </v-alert>
              </v-col>
              <v-col cols="12">
                <v-alert color="primary" border="left" outlined>
                  <v-sheet color="primary dark-1" class="pa-2 white--text" elevation="4">
                    <h2>9.治療項目（Treatment programs）</h2>
                  </v-sheet>
                  <v-row>
                    <v-col cols="12">
                      <v-textarea label="治療項目（Treatment programs）" v-model="list.treatmentprograms"></v-textarea>
                    </v-col>
                  </v-row>
                </v-alert>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType=='add'" :loading="loading" :disabled="loading">確認新增</v-btn>
          <v-btn class="success" @click="editOK" v-if="processType=='edit'" :loading="loading" :disabled="loading">確認修改</v-btn>
        </v-card-actions>
        <!-- <pre>{{list}}</pre>  -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";

export default {
  data() {
    return {
      dialog: false,
      list: {}, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [v => !!v || "不可空白"],
      romItems: ["無障礙", "輕度限制", "中度限制", "重度限制", "完全限制"],
      mmtItems: ["5", "4", "3", "2", "1", "無法施測"],
      actionItems: [
        "床上活動困難",
        "轉位困難",
        "坐姿功能異常",
        "站姿功能異常",
        "步態不穩",
        "上肢功能受限",
        "姿態或動作型態異常",
        "正常"
      ],
      muscletoneItems: ["正常", "高張", "低張"],
      balanceItems: ["良好", "較差", "不佳", "無法評估"],
      brunnstromItems: ["I", "II", "III", "IV", "V", "VI"],
      loading:false,
    };
  },
  props: ["viewData"],
  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function(item) {
      this.dialog = true;
      this.editProcess(item);
    });
  },

  methods: {
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增資料";
      this.titleStyle = "font-weight-black error lighten-2";
      // this.list = {};

      //設定最後一筆資料為新增時的預設值及清除新增中不必要的值
      this.list = { ...this.viewData[0] };
      this.list.snkey = "";
      this.list.date = "";
      this.list.edit_man = "";
    },
    editProcess(item) {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = item; //將頁面資料與父項的資料連結
      this.list = { ...item }; //複制一份實際修改資料內容
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        this.list.u_snkey = this.$store.state.uData.snkey;
        this.list["create_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        this.loading = true;
        this.axios
          .post(
            "general/add/" + this.$store.state.databaseName + "/rehabilitation",
            qs.stringify(this.list)
          )
          .then(rs => {
            // console.log(rs.data)
            if (rs.data.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.list = {};
              this.dialog = false;
              this.$emit("getAllData");
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        this.list["edit_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        this.loading = true;
        this.axios
          .post(
            "general/edit/" + this.$store.state.databaseName + "/rehabilitation",
            qs.stringify(this.list)
          )
          .then(rs => {
            if (rs.data.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;

              for (var i in this.list) {
                //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
                this.oldList[i] = this.list[i];
              }
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    }
  }
};
</script>
